import React from 'react';
import './maze_solver.css';

function heuristic(a, b) {
    let x = a[0] - b[0]
    let y = a[1] - b[1]
    return Math.pow(x * x + y * y, 0.5)
}

function removeFromArr(arr, item) {
    for (let i = arr.length-1; i>=0;i--) {
        if (arr[i] === item) {
            arr.splice(i, 1);
        }
    }
}

function Square(props) {

    let color;
    switch (props.value[0]) {
    case 'Draw':
        color = '#000';
        break;
    case 'Path':
        color = 'blue';
        break;
    case 'Start':
        color = 'green';
        break;
    case 'openSet':
        color = 'green';
        break;
    case 'End':
        color = 'red';
        break;
    case 'closedSet':
        color = 'red';
        break;
    default:
        color = '#fff';
        break;
    }

    return (
        <button className="maze-solver-section"
        style={{
            backgroundColor: color,
        }}
        onClick={() => props.onClick()} />
    )
}

class Cell {
    constructor(i, j, w) {
        this.i = i;
        this.j = j;
        this.type = null;
        this.previous = undefined;
        this.f = 0;
        this.g = 0;
        this.h = 0;
    }


    GetNeighbours(grid) {
        const positions = [[0, 1], [1, 0], [1, 1], [0, -1], [-1, 0], [-1, -1], [1, -1], [-1, 1]];
        let neighbours = [];
        for (let position of positions) {
            let x = this.i + position[0];
            let y = this.j + position[1];
            if (0 <= x && x < grid.length && 0 <= y && y < grid.length) {
                neighbours.push(grid[x][y])
            }
        }
        this.neighbours = neighbours;
    }

}


class MazeSolver extends React.Component {
    constructor(props) {
        super(props);
        const boardLength = 50
        this.tool = 'Draw'
        this.end = undefined;
        this.state = {
            maze: this.reset(boardLength),
            openSet: [],
            closedSet: [],
        }
    }

    gameTick() {
        if (this.end) {
            this.astar()
        } else {
            this.astarSetUp()
        }
    }

    astarSetUp() {

        let openSet = [];

        for (let row of this.state.maze) {
            for (let cell of row) {
                cell.GetNeighbours(this.state.maze)
                if (cell.type === 'Start') {
                    openSet.push(cell)
                } else if (cell.type === 'End') {
                    this.end = cell
                }
            }
        }

        this.setState({
            openSet: openSet,
        })

    }

    astar() {

        let maze = this.state.maze.slice()

        let openSet = this.state.openSet.slice();
        let closedSet = this.state.closedSet.slice();
        const end = this.end
        let path = []

        if (openSet.length > 0) {
            

            let winner = 0;
            for (let i = 0; i < openSet.length; i++) {
                if (openSet[i].f < openSet[winner].f) {
                    winner = i;
                }
            }

            let current = openSet[winner];

            if (current === end) {
                let cell = current;
                path = [cell];
                while (cell.previous) {
                    path.push(cell)
                    cell = cell.previous
                }
                window.fnInterval = clearInterval(window.fnInterval);
            }

            removeFromArr(openSet, current)
            closedSet.push(current)

            for (let neighbour of current.neighbours) {
                if (closedSet.includes(neighbour) || neighbour.type === 'Draw') {
                    continue
                }

                let tentative_g = current.g + 1;
                if (openSet.includes(neighbour)) {
                    if (tentative_g < neighbour.g){
                        neighbour.g = tentative_g
                    }
                } else {
                    neighbour.g = tentative_g
                    openSet.push(neighbour)
                }

                const a = [neighbour.i, neighbour.j];
                const b = [end.i, end.j];

                neighbour.h = heuristic(a, b);

                neighbour.f = neighbour.g + neighbour.h

                neighbour.previous = current
            }

        } else {
            window.fnInterval = clearInterval(window.fnInterval);
            console.log("No solution")
        }


        let openSetType = path.length > 0 ? null : 'openSet'
        let closedSetType = path.length > 0 ? null : 'closedSet'
        for (let row of maze) {
            for (let item of row) {
                if (path.includes(item)) {
                    item.type = 'Path';
                } else if (openSet.includes(item)) {
                    item.type = openSetType;
                } else if (closedSet.includes(item)) {
                    item.type = closedSetType;
                }
            }
        }
        this.setState({
            maze: maze,
            openSet: openSet,
            closedSet: closedSet,
        })
        
    }

    removeAll(maze, tool) {
        for (let row of maze) {
            for (let cell of row) {
                if (cell.type === tool) {
                    cell.type = null;
                }
            }
        }
    }

    draw(i, j) {

        let maze = this.state.maze.slice();

        switch (this.tool) {
        case 'Start':
            this.removeAll(maze,'Start');
            break;
        case 'End':
            this.removeAll(maze, 'End');
            break;
        default:
            break;
        }

        maze[i][j].type = this.tool

        this.setState({
            maze: maze,
        });

    }

    reset(length) {

        let maze = [];

        for (let i = 0; i < length; i++) {
            let row = [];

            for (let j=0; j < length; j++) {
                row.push(new Cell(i, j));
            }

            maze.push(row);
        }

        this.setState({
            maze: maze,
            openSet: [],
            closedSet: [],
        });
        this.end = undefined;

        return maze;
    }

    renderGrid() {

        let grid = [];

        let row = [];
        const options = ['Draw', 'Remove', 'Start', 'End']
        for (let option of options) {
            row.push(<button onClick={() => this.tool = option} className="option">{option}</button>)
        }
        grid.push(<div className="solver-board-row">{row}</div>)

        for (let i = 0; i < this.state.maze.length; i++) {
            let row = []
            for (let j = 0; j < this.state.maze.length; j++) {
                row.push(<Square value={[this.state.maze[i][j].type, this.state.openSet, this.state.closedSet]} onClick={() => this.draw(i, j)}/>)
            }
            grid.push(<div className="solver-board-row">{row}</div>)
        }
        return grid
    }

    render() {

        let grid = this.renderGrid();

        return (
            <div className="board">
                {grid}
                <button onClick={() => window.fnInterval = setInterval(() => { this.gameTick(); }, 50)}>Start</button>
                <button onClick={() => this.reset(this.state.maze.length)}>Reset</button>
            </div>
        )
    }
}

export default MazeSolver;