import React from 'react';
import './snake.css'


class Cell {
    constructor(i, j) {
        this.i = i;
        this.j = j;
        this.snake = false;
        this.fruit = false;
    }
}

class Fruit {
    constructor(size) {
        this.size = size
        this.newCoords();
    }

    newCoords() {
        let allowed = false;
        let coords;

        function checkAllowed(element, _) {
            if (JSON.stringify(element) !== JSON.stringify(coords)) {
                return true;
            }
            return false;
        }

        while (!allowed) {
            coords = [Math.floor(Math.random() * this.size[0]), Math.floor(Math.random() * this.size[1])];
            allowed = window.snake.memory.every(checkAllowed)
        }
        this.coords = coords;
    }
}

class Snake {
    constructor(size) {
        this.score = 0;
        this.memory = [[Math.round(size[0]/2), Math.round(size[1]/2)]]
        this.move = {'up': [0, -1], 'right':[1, 0], 'down':[0, 1], 'left':[-1, 0]}
        this.direction = 'up'
    }

    movement() {
        const new_x = this.memory[0][0] + this.move[this.direction][0];
        const new_y = this.memory[0][1] + this.move[this.direction][1];
        const newCoord = [new_x, new_y];
        this.memory.unshift(newCoord)
        while (this.score < this.memory.length - 1) {
            this.memory.pop()
        }
    }
}

function Score(props) {
    return (
        <h1 className="score">Score: {props.value}</h1>
    )
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.size = [40, 25];
        const gameBoard = this.newBoard();
        window.snake = new Snake(this.size);
        this.fruit = new Fruit(this.size)
        this.state = {
            gameBoard: gameBoard,
            tickTime: 300,
        }
        window.fnInterval = setInterval(() => { this.gameTick(); }, this.state.tickTime);
    }

    gameTick() {
        window.snake.movement();
        this.checkCollision();
        this.updateBoard();
    }

    checkCollision() {
        const head = window.snake.memory[0]
        if (JSON.stringify(this.fruit.coords) === JSON.stringify(head)) {
            this.fruit.newCoords();
            window.snake.score++;
        }

        const exited = (0 > head[0] || this.size[0] < head[0] || 0 > head[1] || this.size[1] < head[1])

        let hitSelf = false;
        for (let body of window.snake.memory.slice(1, )) {
            if (JSON.stringify(head) === JSON.stringify(body)) {
                hitSelf = true;
            }
        }
        if (exited || hitSelf) {
            window.fnInterval = clearInterval(window.fnInterval)
            console.log("Game Over")
        }
    }

    updateBoard() {

        let board = this.newBoard()

        for (let snake of window.snake.memory) {
            try {
                board[snake[0]][snake[1]].snake = true;
            } catch {
                console.log("dead")
            }

        }

        board[this.fruit.coords[0]][this.fruit.coords[1]].fruit = true;
                

        this.setState({
            gameBoard: board,
        });
    }

    newBoard() {
        let board = [];
        for (let i = 0; i < this.size[0]; i++) {
            let row = []
            for (let j = 0; j < this.size[1]; j++) {
                row.push(new Cell(i, j))
            }
            board.push(row);
        }
        return board
    }

    renderBoard() {
        let renderBoard = [];
        for (let i = 0; i < this.size[0]; i++) {
            let row = [];
            for (let j = 0; j < this.size[1]; j++) {
                let color;
                if (this.state.gameBoard[i][j].snake) {
                    color = '#fff';
                } else if (this.state.gameBoard[i][j].fruit) {
                    color = "red"
                } else {
                    color = '#000'
                }
                row.push(<div className="boardSegment" style={{backgroundColor: color}} />)
            }
            renderBoard.push(<div className="board-row">{row}</div>)
        }

        return renderBoard;
    }

    render() {
        var renderedBoard = this.renderBoard()
        return (
            <div className="board">
                {renderedBoard}
                <Score value={window.snake.score} />
                <UserInput />
            </div>
        )
    }
}

class UserInput extends React.Component {
    render() {
        return (
            <div />
        )
    }  

    keyHandling(e) {
        switch (e.keyCode) {
        case 37:
            if (!(window.snake.direction === 'right')) {
                window.snake.direction = 'left';
            }
            break;
        case 38:
            if (!(window.snake.direction === 'down')) {
                window.snake.direction = 'up';
            }
            break;
        case 39:
            if (!(window.snake.direction === 'left')) {
                window.snake.direction = 'right';
            }
            break;
        case 40:
            if (!(window.snake.direction === 'up')) {
                window.snake.direction = 'down';
            }
            break;
        default: 
            break;
        }
    }
      
    componentDidMount() {
        // Add Event Listener on compenent mount
        window.addEventListener("keyup", this.keyHandling);
    }
      
    componentWillUnmount() {
        // Remove event listener on compenent unmount
        window.removeEventListener("keyup", this.keyHandling);
    }
}

export default Game;