import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {
	HashRouter as Router,
	Switch,
	Route,
  } from "react-router-dom";

import PasswordChecker from './password_checker.js';
import Board from './noughts_and_crosses.js';
import Sudoku from './sudoku.js';
import MazeGen from './maze_generator.js';
import Snake from './snake.js';
import Conways from './conways.js';
import MazeSolver from './maze_solver.js';
import Chess from './chess.js';
import LofTF from './lord_of_the_flies.js';


const url = "https://ogreenwood672.github.io/website/#/"
//const url = "localhost:3000/website/#/"

function NavButton(props) {
	return (
		<button className="navbutton" onClick={() => {props.onClick()}}>
			{props.value}
		</button>
	);
}

class Navbar extends React.Component {

	handleClick(message) {
		if (message === "HOME") {
			window.location.href = url;
			return;
		}
		window.location.href = url + message.toLowerCase();
	}

	renderButton(message) {
		return	<NavButton 
				value={message} 
				onClick={() => this.handleClick(message)}
				/>
	}

	render() {
		return (
			<div className="navbar">
				<div className="leftNavbar">
					{this.renderButton("HOME")}
					{this.renderButton("About")}
				</div>
			</div>
		);
	}
}

class ProjectNav extends React.Component {


	render() {
		const origin = url
		return (
			<div className="rightProjects">
				<div className="dropdown">
					<button className="navbutton">Algorithms</button>
					<div className="dropdown-content">
						<a href={origin+"chess"}>Chess</a>
						<a href={origin+"sudoku"}>Sudoku</a>
						<a href={origin+"noughts_and_crosses"}>Noughts and Crosses</a>
						<a href={origin+"maze_generator"}>Maze Generator</a>
						<a href={origin+"maze_solver"}>Maze Solver</a>
					</div>
				</div>
				<div className="dropdown">
					<button className="navbutton">Games</button>
					<div className="dropdown-content">
						<a href={origin+"snake"}>Snake</a>
						<a href={origin+"conways_game_of_life"}>Conway's Game of Life</a>
					</div>
				</div>
				<div className="dropdown">
					<button className="navbutton">Text Based</button>
					<div className="dropdown-content">
						<a href={origin+"password_checker"}>Password Checker</a>
						<a href={origin+"lord_of_the_flies"}>LofTF</a>
					</div>
				</div>
			</div>
		);

		//<a href={origin+"agar.io"}>Agar.io</a>
	}
}


class About extends React.Component {
	render() {
		return (
			<div className="mainBox center">
				<h1>&lt;Insert inspirational quote here&gt;</h1>
			</div>
		)
	}
}

class Home extends React.Component {
	render() {
		return (
			<div className="mainBox center">
				<h1>Good Afternoon</h1>
			</div>
		)
	}
}

class Website extends React.Component {
  	render() {

    	return (
			<Router>
				<div className="background">
					<Navbar />
					<ProjectNav />
					<Switch>
						<Route path="/lord_of_the_flies">
							<LofTF />
						</Route>
						<Route path="/password_checker">
							<PasswordChecker />
						</Route>
						<Route path="/noughts_and_crosses">
							<Board />
						</Route>
						<Route path="/sudoku">
							<Sudoku />
						</Route>
						<Route path="/maze_generator">
							<MazeGen />
						</Route>
						<Route path="/snake">
							<Snake />
						</Route>
						<Route path="/conways_game_of_life">
							<Conways />
						</Route>
						<Route path="/maze_solver">
							<MazeSolver />
						</Route>
						<Route path="/chess">
							<Chess />
						</Route>
						<Route path="/about">
							<About />
						</Route>
						<Route exact path="/">
							<Home />
						</Route>
					</Switch>
				</div>
			</Router>
    	)
  	}
}


ReactDOM.render(
	<Website/>,
  	document.getElementById('root')
);

