function TextInput(props) {
    return (
        <div>
            <input value={props.value} onChange={evt => props.onChange(evt)}/>
        </div>
    );
}

class PasswordChecker extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            inputValue: "",
            color: "red",
            endText: "INVALID"
        };
    }

    updateColor() {
        var sum = 0
        const whiteSpace = /\s/;
        const upper = /[A-Z]+/;
        const lower = /[a-z]+/;
        const digits = /\d+/;
        const string = this.state.inputValue;
        if (!whiteSpace.test(string)) {
            this.setState({endText: "STRENGTH"});
            if (upper.test(string)) { sum+=5; }
            if (lower.test(string)) { sum+=5; }
            if (digits.test(string)) { sum+=5; }
            if (string.length < 8) {
                sum -= 15;
            } else if (sum >= 10) {
                sum += string.length;
            }

            if (sum > 20) {
                return "green";
            } else if (sum > 12) {
                return "orange";
            } else {
                return "red";
            }
        }
        this.setState({endText: "INVALID"});
        return "red";
    }

    updateInputValue(evt) {
        this.setState({
            inputValue: evt.target.value
        });
        this.setState({color: this.updateColor()});
    }

    render() {
        return (
            <div className="center mainBox">
                <p className="instructions">Password: </p>
                <TextInput value={this.state.inputValue} onChange={(evt) => this.updateInputValue(evt)}/>
                <p className="instructions" style={{color: this.state.color}}>****STRENGTH****</p>
            </div>
        )
    }
}

export default PasswordChecker;