import React from 'react';
import './noughts_and_crosses.css';

class Ai {

  static mainMinimax(squares) {
    let bestScore = -Infinity;
    let bestMove;
    let newScore;
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        squares[i] = 'O';
        newScore = this.subMinimax(squares, false, 1);
        squares[i] = null;
        console.log(newScore);
        if (newScore > bestScore) {
          bestScore = newScore;
          bestMove = i;
        }
      }
    }
    return bestMove;

  }

  static subMinimax(squares, minimax, depth) {

    let bestScore;
    let newScore;

    const points = {'X': -900, 'O': 1000};
    let winner = calculateWinner(squares);

    if (winner in points) {
      return points[winner];
    } else if (!squares.includes(null)) {
      return 1;
    }

    if (minimax) {
      bestScore = -Infinity
      for (let i = 0; i < squares.length; i++) {
        if (squares[i] === null) {
          squares[i] = 'O';
          newScore = this.subMinimax(squares, !minimax, depth+1) - depth;
          squares[i] = null;
          bestScore = Math.max(newScore, bestScore)
        }
      }
    } else {
      bestScore = Infinity
      for (let i = 0; i < squares.length; i++) {
        if (squares[i] === null) {
          squares[i] = 'X';
          newScore = this.subMinimax(squares, !minimax, depth+1) - depth;
          squares[i] = null;
          bestScore = Math.min(newScore, bestScore)
        }
      }
    }
    //console.log(bestScore)
    return bestScore;
  }
}

function Square(props) {
  return (
    <button className="square" 
              onClick={() => {props.onClick()}}
            >
        {props.value}
      </button>
  )
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      squares: Array(9).fill(null),
    };
  }

  handleClick(i) {
    const squares = this.state.squares.slice();

    if (calculateWinner(squares) || squares[i]) {
      return;
    }

    squares[i] = 'X';

    if (!calculateWinner(squares)) {
      let aiMove = Ai.mainMinimax(squares);
      console.log(aiMove)
      squares[aiMove] = 'O';
    }

    this.setState({squares: squares});
  }

  reset() {
    this.setState({squares: Array(9).fill(null)});
  }

  renderSquare(i) {
    return <Square 
            value={this.state.squares[i]}
            onClick={() => this.handleClick(i)}
            />
  }

  render() {
    const winner = calculateWinner(this.state.squares);
    let status;
    if (winner) {
      status = 'Winner: ' + winner;
    } else {
      status = "You are: 'X'";
    }


    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
        <div className="status">{status}</div>
        <button id="reset" onClick={() => this.reset()}>Reset</button>
      </div>
    );
  }
}

class Game extends React.Component {
  render() {
    return (
      <div className="game-board">
        <Board />
      </div>
    );
  }
}

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

// ========================================

export default Game
