import React from 'react';
import './sudoku.css'

function Square(props) {
    return (
        <button className="squareSudoku" onClick={(evt) => {props.onClick(evt)}}>{props.value}</button>
      )
}

function difference(setA, setB) {
    let _difference = new Set(setA)
    for (let elem of setB) {
        _difference.delete(elem)
    }
    return _difference
}

class Solver {
    constructor(board) {
        this.board = board;
    }

    CheckIfPossible() {
        for (let i = 1; i < 10; i++) {
            for (let row = 0; row < 9; row++) {
                let rowItems = 0;
                for (let item of this.board[row]) {
                    if (item === i) {
                        rowItems++;
                    }
                }
                if (rowItems > 1) {
                    return false;
                }
            }

            for (let column = 0; column < 9; column++) {
                let columnItems = 0;
                for (let r = 0; r < 9; r++) {
                    if (this.board[r][column] === i) {
                        columnItems++
                    }
                }
                if (columnItems > 1) {
                    return false
                }
            }
            const boxes = [[0, 0], [0, 3], [3, 0], [3, 3], [6, 0], [0, 6], [6, 3], [3, 6], [6, 6]];
            for (let box of boxes) {
                let boxCount = 0;
                for (let x = 0; x < 3; x++) {
                    for (let y = 0; y < 3; y++) {
                        if (this.board[box[0]+x][box[1]+y] === i) {
                            boxCount++;
                        }
                    }
                }
                if (boxCount > 1) {
                    return false;
                }
            }
        }
        console.log("Solvable")
        return true;
    }
    
    findAvaliable(originalI, originalJ) {
        var numbers = new Set([1, 2, 3, 4, 5, 6, 7, 8, 9]);
        var taken = new Set();
        //Row
        for (let j = 0; j < 8; j++) {
            if (this.board[originalI][j]) {
                taken.add(this.board[originalI][j]);
            }
        }
        //Column
        for (let i = 0; i < 8; i++) {
            if (this.board[i][originalJ]) {
                taken.add(this.board[i][originalJ]);
            }
        }

        //Get top left box coords
        const boxes = [[0, 1, 2],[3, 4, 5], [6, 7, 8]];
        let iBox, jBox, box;
        for (box of boxes) {
            if (box.includes(originalI)) {
                iBox = box[0];
            }
            if (box.includes(originalJ)) {
                jBox = box[0];
            }
        }

        //Box
        for (let i = 0; i < 3; i++) {
            for(let j = 0; j < 3; j++) {
                const num = this.board[iBox+i][jBox+j];
                if (num) {
                    taken.add(num);
                }
            }
        }

        return Array.from(difference(numbers, taken));
       
    }

    updateIJ(i, j) {
        j++;
        if (j > 8) {
            j = 0;
            i++;
        }
        return [i, j];
    }

    solve(i, j) {
        if (i > 8) {
            return true;
        }

        if (!this.board[i][j]) {
            const avaliable = this.findAvaliable(i, j)

            let num;
            for (num of avaliable) {
                this.board[i][j] = num;
                let [newI, newJ] = this.updateIJ(i, j);
                if (this.solve(newI, newJ)) {
                    return true;
                }
            }
            this.board[i][j] = null;
            return false;
        }

        let [newI, newJ] = this.updateIJ(i, j);
        if (this.solve(newI, newJ)) {
            return true;
        }
        return false
    }
}

class Sudoku extends React.Component {
    constructor(props) {
        super(props);
        const board = [];
        for (let i=0; i<9; i++) {
            board.push(new Array(9).fill(null));
        }
        this.state = {
            board: board,
            option: "delete"
        }
    }

    reset() {
        var newBoard = [];
        for (let i=0; i<9; i++) {
            newBoard.push(new Array(9).fill(null));
        }
        this.setState({
            board: newBoard
        });
    }

    updateValue(i, j) {
        var newBoard = this.state.board.slice();
        const opt = this.state.option
        if (opt !== "delete") {
            newBoard[i][j] = this.state.option;
        } else {
            newBoard[i][j] = null;
        }
        this.setState({
            board: newBoard,
        });
    }

    renderRow(i) {
        let row = []
        for (let j=0; j<9; j++) {
            row.push(<Square value={this.state.board[i][j]} onClick={() => this.updateValue(i, j)}/>)
        }
        return row
    }

    solve() {
        const ai = new Solver(this.state.board);
        if (ai.CheckIfPossible()) {
            ai.solve(0, 0);
            this.setState({board: ai.board});
        }
    }

    render() {

        var rows = [];
        for (let i = 0; i < 9; i++) {
            rows.push(<div className="board-row-sudoku">{this.renderRow(i)}</div>)            
        }

        var options = []
        for (let i = 1; i < 10; i++) {
            options.push(<div className="board-row-sudoku"><button className="squareSudoku" onClick={() => this.setState({option: i})}>{i}</button></div>)
        }
        options.push(<div className="board-row-sudoku"><button id="delete" onClick={() => this.setState({option: "delete"})}>Delete</button></div>)

        return (
            <div>
                <div className="options">
                    {options}
                </div>
                <div className="sudokuBoard">
                    {rows}
                    <button className="btn" id="resetSudoku" onClick={() => this.reset()}>Reset</button>
                    <button className="btn" id="solve" onClick={() => this.solve()}>Solve</button>
                </div>
            </div>
        )
    }
}

export default Sudoku;