import React from 'react';
import './conways.css'

class Cell {
    constructor(i, j) {
        this.i = i;
        this.j = j;
        this.alive = false;
    }

    GetNeighbours(board) {
        const positions = [[0, 1], [1, 0], [1, 1], [0, -1], [-1, 0], [-1, -1], [1, -1], [-1, 1]];
        let neighbours = 0
        for (let position of positions) {
            let x = this.i + position[0];
            let y = this.j + position[1];
            if (0 <= x && x < board.length && 0 <= y && y < board[0].length) {
                //console.log(x, y)
                if (board[x][y].alive) {
                    neighbours++;
                }
            }
        }
        this.neighbours = neighbours;
    }

}

function Go(props) {
    return <button onClick={() => props.onClick()}>Start</button>
}

function Stop(props) {
    return <button onClick={() => props.onClick()}>Stop</button>
}

class Conways extends React.Component {
    constructor(props) {
        super(props);
        this.size = [40, 25];
        const gameBoard = this.newBoard();
        this.state = {
            gameBoard: gameBoard,
            tickTime: 1000,
            x: 0,
            y: 0,
        }
    }

    gameTick() {
        this.updateBoard();
    }

    begin() {
        window.fnInterval = setInterval(() => { this.gameTick(); }, this.state.tickTime);
    }

    end() {
        window.fnInterval = clearInterval(window.fnInterval)
    }

    changeState(i, j) {
        let board = this.state.gameBoard;

        board[i][j].alive = !board[i][j].alive

        this.setState({
            gameBoard: board,
        })

    }

    updateBoard() {

        let board = this.state.gameBoard;
        let change;
        for (let row of board) {
            for (let indiv of row) {
                indiv.GetNeighbours(board)
                if (indiv.neighbours === 3 && !indiv.alive) {
                    indiv.future = true;
                    change = true;
                } else if (indiv.alive && (indiv.neighbours === 2 || indiv.neighbours === 3)) {
                    indiv.future = true;
                } else {
                    if (indiv.alive) {
                        change = true;
                    }
                    indiv.future = false;
                }
            }
        }
        for (let row of board) {
            for (let indiv of row) {
                indiv.alive = indiv.future;
            }
        }

        if (!change) {
            this.end()
        }
                
        this.setState({
            gameBoard: board,
        });
    }

    newBoard() {
        let board = [];
        for (let i = 0; i < this.size[0]; i++) {
            let row = []
            for (let j = 0; j < this.size[1]; j++) {
                row.push(new Cell(i, j))
            }
            board.push(row);
        }
        return board
    }

    renderBoard() {
        let renderBoard = [];
        for (let i = 0; i < this.size[0]; i++) {
            let row = [];
            for (let j = 0; j < this.size[1]; j++) {
                let color = this.state.gameBoard[i][j].alive ? '#fff' : '#000'
                row.push(<button style={{backgroundColor: color}} className="game-board-section" onClick={() => this.changeState(i, j)} />);
            }
            renderBoard.push(<div className="game-board-row">{row}</div>)
        }

        return renderBoard;
    }

    render() {
        var renderedBoard = this.renderBoard()
        return (
            <div className="gameBoard">
                {renderedBoard}
                <Go onClick={() => this.begin()} />
                <Stop onClick={() => this.end()} />
            </div>
        )
    }
}

export default Conways;