import React from 'react';
import './maze_generator.css';

function WidthInput(props) {
    return <input  onChange={evt => props.onChange(evt)} />
}

function Square(props) {

    function borderline(b) {
        if (b) {
            return "black";
        } else { return "white" }
    }

    return (
        <div className="mazeSquare"
        style={{
            backgroundColor: props.value.color,
            width: 500 / props.value.w,
            height: 500 / props.value.w,
            borderTop: "1px solid " + borderline(props.value.walls[0]),
            borderRight: "1px solid " + borderline(props.value.walls[1]),
            borderBottom: "1px solid " + borderline(props.value.walls[2]),
            borderLeft: "1px solid " + borderline(props.value.walls[3]),
        }} />
    )
}

class Cell {
    constructor(i, j, w) {
        this.walls = [true, true, true, true];
        this.visited = false;
        this.i = i;
        this.j = j;
        this.w = w;
        this.color = "#000";

    }

    getNeighbours(grid) {
        let neighbours = []

        if (0 <= this.i-1 && this.i-1 < this.w) {
            const top = grid[this.i-1][this.j]
            if (!top.visited) {
                neighbours.push(top)
            }
        }

        if (0 <= this.j+1 && this.j+1 < this.w) {
            const right = grid[this.i][this.j+1]
            if (!right.visited) {
                neighbours.push(right)
            }
        }

        if (0 <= this.i+1 && this.i+1 < this.w) {
            const bottom = grid[this.i+1][this.j]
            if (!bottom.visited) {
                neighbours.push(bottom)
            }
        }

        if (0 <= this.j-1 && this.j-1 < this.w) {
            const left = grid[this.i][this.j-1]
            if (!left.visited) {
                neighbours.push(left)
            }
        }

        
        this.neighbours = neighbours
    }
}

function removeWalls(a, b) {
    const x = a.j - b.j;
    const y = a.i - b.i;
    if (y === 1) {
        a.walls[3] = false;
        b.walls[1] = false;
    }
    if (y === -1) {
        a.walls[1] = false;
        b.walls[3] = false;
    }

    if (x === 1) {
        a.walls[0] = false;
        b.walls[2] = false;
    }
    if (x === -1) {
        a.walls[2] = false;
        b.walls[0] = false;
    }

}

class MazeGen extends React.Component {
    constructor(props) {
        super(props);
        const boardLength = 10
        this.state = {
            maze: this.reset(boardLength),
            boardLength: boardLength
        }
    }

    create(maze) {
        if (maze[0][0].visited) {
            return;
        }

        const visitColor = "#fff"

        maze[0][0].visited = true;
        maze[0][0].color = visitColor
        let stack = [maze[0][0]];
        let end = [0, null];

        while (stack.length > 0) {
            
            let current = stack.pop();
            current.getNeighbours(maze);

            if (stack.length > end[0]) {
                end = [stack.length, current];
            }

            if (current.neighbours.length !== 0) {
                stack.push(current);
                let neighbour = current.neighbours[Math.floor(Math.random() * current.neighbours.length)];
                removeWalls(current, neighbour);
                neighbour.visited = true;
                neighbour.color = visitColor;
                stack.push(neighbour);
            }

        }

        maze[0][0].color = "red"
        end[1].color = "red"
        console.log("Created")
        this.setState({
            maze: maze
        });
    }

    updateBoard(evt) {
        const digit = /\d+/;
        if (digit.test(evt.target.value)) {
            if (evt.target.value <= 100) {
                this.setState({
                    boardLength: evt.target.value,
                });
            }
        }
    }

    reset(length) {
        let maze = [];
        for (let i = 0; i < length; i++) {
            let row = [];
            for (let j=0; j < length; j++) {
                row.push(new Cell(i, j, length))
            }
            maze.push(row)
        }

        this.setState({
            maze: maze
        });

        return maze;
    }

    renderGrid() {
        let grid = [];
        for (let i = 0; i < this.state.maze.length; i++) {
            let row = []
            for (let j = 0; j < this.state.maze.length; j++) {
                let info = {walls: this.state.maze[i][j].walls, w: this.state.maze[i][j].w, color: this.state.maze[i][j].color}
                row.push(<Square value={info}/>)
            }
            grid.push(<div className="board-row">{row}</div>)
        }
        return grid
    }

    render() {

        let grid = this.renderGrid();

        return (
            <div>
                <div className="wInput">
                    <WidthInput onChange={evt => this.updateBoard(evt)}/>
                </div>
                <div className="board">
                    {grid}
                    <button onClick={() => this.create(this.state.maze.slice())}>Create</button>
                    <button onClick={() => this.reset(this.state.boardLength)}>Reset</button>
                </div>
            </div>
        )
    }
}

export default MazeGen;